.node {
  width: 25px;
  height: 25px;
  border: 1px rgb(175, 216, 248);
  border-style: solid;
}

.node.visited {
  animation-name: visitedNode;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-fill-mode: forwards;
}

.node.path {
  border: 1px rgb(255, 254, 106);
  border-style: solid;

  background-color: rgb(255, 254, 106);
  animation-name: createPath;
  animation-duration: 2s;
}

.node.wall {
  border: 1px rgb(12, 53, 71);
  border-style: solid;

  background-color: rgb(12, 53, 71);
  animation-name: createWall;
  /* duration connects to timeout */
  animation-duration: 3s;
  animation-timing-function: ease-out;
}

.start {
  background-image: url("../pictures/start_point_black.png");
  background-repeat: no-repeat;
  background-position: center;

  animation: createStartDestination;
  animation-duration: 1s;
  animation-timing-function: ease-out;
}

.start.red {
  background-image: url("../pictures/start_point_red.png");
}

.destination {
  background-image: url("../pictures/destination2_red.png");
  background-repeat: no-repeat;
  background-position: center;

  animation: createStartDestination;
  animation-duration: 1s;
  animation-timing-function: ease-out;
}

.destination.black {
  background-image: url("../pictures/destination2_black.png");
}

@keyframes createWall {
  0% {
    transform: scale(0.1);
  }
  50% {
    transform: scale(1.25);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes createStartDestination {
  0% {
    transform: scale(0.3);
  }
  50% {
    transform: scale(1.25);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes visitedNode {
  0% {
    transform: scale(0.25);
    background-color: rgba(10, 9, 66, 0.85);
    /* background-color: lightyellow; */
    border-radius: 100%;
  }
  35% {
    transform: scale(0.5);
    background-color: rgba(15, 100, 213, 0.85);
  }
  75% {
    transform: scale(1.2);
    background-color: rgba(5, 222, 160, 0.85);
  }
  100% {
    transform: scale(1);
    background-color: rgba(50, 190, 218, 0.85);
  }
}

@keyframes createPath {
  0% {
    transform: scale(0.4);
    background-color: rgb(255, 136, 0);
  }
  50% {
    transform: scale(1.4);
    background-color: rgb(236, 215, 22);
  }
  100% {
    transform: scale(1);
    background-color: rgb(255, 254, 106);
  }
}
