#top-menu {
  position: absolute;
  display: flex;
  align-items: center;
  width: 100%;
  height: 9%;
  background-color: #00695c;
}

.grid {
  position: absolute;
  bottom: 10px;
}

.row {
  display: flex;
}

.element-top-menu {
  cursor: pointer;
  display: inline-block;
  color: white;
  font-size: 18px;
  font-weight: bold;
  font-family: monospace;
}

.separator {
  display: inline-block;
  margin: 0 20px;
}

.algo {
  font-size: 20px;
}

.algo:hover {
  color: lightsalmon;
}

.normal-cursor {
  cursor: default !important;
}

.range {
  display: inline-block;
  width: 150px;
  margin-left: 1rem;
}

/* Override */

.input-range {
  height: 0.7rem !important;
}

.input-range__track--active {
  background: pink !important;
}

.input-range__slider {
  background: pink !important;
  border: pink !important;
}

.input-range__label {
  color: lightgoldenrodyellow !important;
}
